
.crea{
    font-size: 30px;
    font-family: 'Gill Sans', 'Gill Sans MT';
    animation: crea 3s infinite alternate-reverse;
}

@keyframes crea {
    from {margin: 0%;
        width: -100%;
        margin-right: -50px;
        }
    to { margin-right: 100%;
        width: 300%;
        }
}